/* global angular, $ */
angular.module('ua5App')
    .directive('beforeAfter', ['$timeout', function($timeout) {
        return {
            restrict: 'A',
            templateUrl: 'components/before-after/before-after.html',
            scope: {
                beforeAfterBefore: '@',
                beforeAfterAfter: '@'
            },
            link: function($scope, element, attrs) {
                var $$element;
                var $$beforeElement;
                var $$images;
                var isMouseDown = false;

                function resizeImage() {
                    $$images.eq(1).width($$images.eq(0).width());
                }

                function init() {
                    $$element.bind('touchstart mousedown', function(e) {
                        e.stopPropagation();
                        e.preventDefault();
                        isMouseDown = true;
                    }).bind('mousemove', function(e) {
                        e.stopPropagation();
                        e.preventDefault();
                        if (isMouseDown) {
                            var elementOffset = $(element).offset();
                            var elementWidth = $(element).width();
                            var relX = e.pageX - elementOffset.left;
                            if (relX >= 0 && relX <= elementWidth) {
                                $$beforeElement.width(relX);
                            }
                        }
                    }).bind('touchmove', function(e) {
                        e.stopPropagation();
                        e.preventDefault();
                        if (isMouseDown) {
                            var elementOffset = $(element).offset();
                            var elementWidth = $(element).width();
                            var relX = e.originalEvent.touches[0].pageX - elementOffset.left;
                            if (relX >= 0 && relX <= elementWidth) {
                                $$beforeElement.width(relX);
                            }
                        }
                    }).bind('touchend mouseup', function() {
                        isMouseDown = false;
                    });

                    $$element.imagesLoaded(function() {
                        $(element).css('max-width', $$images.eq(0).width() + 'px');
                    });

                    resizeImage();
                }

                $timeout(function() {
                    $$element = $('.before-after', element);
                    $$beforeElement = $('.before-after__before', $$element);
                    $$images = $('img', $$element);
                    $$images.eq(0).load(function() {
                        resizeImage();
                    });
                    init();
                });

                $scope.$on('app:resized', resizeImage);
            }
        };
    }])
;

