                                description: 'While a tuxedo should never be worn with a belt, suits are a different story. You can wear a belt with a suit (just be sure to match your belt color to your shoes), opt for suspenders, or go without. Many men feel like their look is “unfinished” if they leave the belt loops on their pants unoccupied, but keep this in mind: your jacket will cover up the belt loops anyway, so who would know either way? If you wear a suit with a belt and no one sees it, does the belt even exist? Just a couple philosophical questions to ponder while you’re deciding whether to wear a belt.',
