                                description: 'If your tuxedo pants have belt loops on them, then they are not tuxedo pants. If you’re looking for some assistance in holding up your tuxedo pants on the dance floor, then your only option is to wear suspenders—and preferably the traditional button style (also known as braces) over clip-ins.',
