                            description: 'Cotton is breathable, making it perfect for dress shirts. Some manufacturers use polyester to cut costs, adding durability and wrinkle resistance, but poly reduces breathability. What does that mean for you? Shirts that get very, very sweaty. You might spend three minutes ironing a cotton shirt, but at least it’s not a biohazard. Cotton is also great for pocket squares and ties, adding crisp textural variety against a wool jacket.'
