/* global angular, $, _ */
angular.module('ua5App')
    .directive('equalize', ['$timeout', function($timeout) {
        return {
            restrict: 'A',
            scope: {
                equalizeChildren: '=',
                equalizeBreakpoint: '@'
            },
            link: function($scope, element, attrs) {
                var $element;
                // Reset heights of children back to their default height
                function reset() {
                    _.each($scope.equalizeChildren, function(child) {
                        $element.find(child).css('height', '');
                    });
                }
                // Equalize the height of child elements
                function equalize() {
                    // Only apply if window width is greater than breakpoint
                    // or if breakpoint is `undefined`.
                    if (window.innerWidth > $scope.equalizeBreakpoint || !$scope.equalizeBreakpoint) {
                        _.each($scope.equalizeChildren, function(child) {
                            $element.equalize({
                                children: child,
                                reset: true
                            });
                        });
                    } else {
                        reset();
                    }
                }
                $timeout(function() {
                    $element = $(element);
                    equalize();
                });
                $scope.$on('app:resized', equalize);
            }
        };
    }])
;

