/* global angular, $ */
angular.module('ua5App')
    .directive('flyout', ['chapters', '$state', '$rootScope', function(chapters, $state, $rootScope) {
        return {
            restrict: 'A',
            templateUrl: 'components/flyout/flyout.html',
            link: function($scope, element, attrs) {
                $scope.open = false;
                chapters.get().then(function(response) {
                    $scope.flyoutChapters = response;
                });

                $scope.closeFlyout = function() {
                    $scope.open = false;
                    $scope.$applyAsync();
                };

                $scope.goHome = function() {

                    $scope.closeFlyout();

                    if ($state.current.name === 'home') {
                        $(window).scrollTop(0);
                    } else {

                        $state.go('home', {
                            chapter: null
                        }, {
                            reload:true,
                            notify: true
                        });
                    }
                };

                $scope.gotoHomeChapter = function(slug) {

                    $scope.closeFlyout();

                    if ($state.current.name !== 'home') {
                        $state.go('home', {
                            chapter: slug
                        }, {
                            reload:true,
                            notify: true
                        });
                    } else {
                        $rootScope.$broadcast('nav:chapter', slug);
                    }
                };

                $scope.$on('flyout:open', function() {
                    $scope.open = true;
                    $('.flyout__main').scrollTop(0);
                });
                $scope.$on('app:escape:pressed', $scope.closeFlyout);
                $scope.$on('$stateChangeStart', $scope.closeFlyout);
            }
        };
    }])
;

/* global angular, TweenMax, $ */
angular.module('ua5App')
    .directive('groomsGuideEmergency', ['emergencyCase', function(emergencyCase) {
        return {
            restrict: 'A',
            templateUrl: 'components/grooms-guide-emergency/grooms-guide-emergency.html',
            link: function($scope, element, attrs) {
                var $$expandButtons = $('.gg-emergency__expand', element);

                function initExpand() {
                    $$expandButtons.each(function() {
                        var $$this = $(this);
                        var $$prev = $$this.prev().eq(0);

                        TweenMax.set($$prev, {
                            height: 0
                        });

                        $$prev.expanded = false;
                        $$this.addClass('closed');

                        $$this.click(function() {
                            if ($$prev.expanded) {
                                TweenMax.to($$prev, 0.5, {
                                    height: 0
                                });
                                $$prev.expanded = false;
                                $$this.addClass('closed');
                            } else {
                                TweenMax.set($$prev, {height: 'auto'});
                                TweenMax.from($$prev, 0.5, {
                                    height: 0
                                });
                                $$prev.expanded = true;
                                $$this.removeClass('closed');
                            }
                        });
                    });
                }

                emergencyCase.get().then(function(response) {
                    $scope.data = response;
                    initExpand();
                });
            }
        };
    }])
;

/* global angular, $, _, TweenMax, WebKitCSSMatrix */
angular.module('ua5App')
    .directive('header', ['$timeout', 'COLORS', '$state', 'chapters', 'sections', '$location', '$rootScope', function($timeout, COLORS, $state, chapters, sections, $location, $rootScope) {
        return {
            restrict: 'A',
            templateUrl: 'components/header/header.html',
            link: function($scope, element, attrs) {
                var headerHeight;
                var staticHeight;
                var sectionsListHeight;
                var $$window = $(window);
                var $$header = $('.header', element);
                var $$headerMain = $('.header__main', element);
                var $$headerSection = $('.header__section', element);
                var $$headerSectionsList = $('.header__sections-list', element);
                var $$headerInner = $('.header__main__inner', element);
                var $$headerStatic = $('.header__static', element);
                var $$titleNumber = $('.header__main__chapter-title__number', element);
                var sectionHeaderCollapseMin = 150;

                // this is for the home scrolling stuff
                var pageElement;
                var isScrolling = false;
                var calculateInterval = null;
                var calculateTime = 20;

                var browser = {
                    isIe: (navigator.userAgent.indexOf('MSIE ') > 0) || !!navigator.userAgent.match(/Trident.*rv\:11\./)
                };

                $scope.headerShowing = false;
                $scope.headerStaticShowing = false;
                $scope.headerSectionShowing = false;
                $scope.headerSectionCollapsed = false;
                $scope.headerSectionsListShowing = false;
                $scope.headerSectionsListClosing = false;

                $scope.headerCurrentSection = false;

                $scope.isMobile = false;

                $scope.homeChapters = [];
                $scope.title = null;
                $scope.showTitle = false;
                $scope.currentChapter = null;
                $scope.currentSection = null;
                $scope.currentArticle = null;

                $scope.chapterNav = {};

                $scope.goToAnchor = function(chapter, section, anchor) {

                    if (chapter !== $scope.currentChapter || section !== $scope.currentSection || ($scope.currentArticle && section && chapter)) {
                        $scope.goToSection(chapter.id, section.slug);

                        $timeout(function() {
                            $$window.scrollTop($('[name="' + anchor + '"]').offset().top - 70);
                            $location.hash(anchor);
                        }, 1200);
                    } else {
                        $$window.scrollTop($('[name="' + anchor + '"]').offset().top - 70);
                        $location.hash(anchor);
                    }
                };

                $scope.goHomeFromHeader = function() {
                    var options = {
                        reload: true,
                        notify: true
                    };

                    if ($state.current.name === 'home') {
                        options.reload = false;
                        options.notify = false;
                    }

                    $$window.scrollTop(0);
                    $state.go('home', {
                        chapter: null
                    }, options);

                    $scope.headerStaticShowing = true;
                    $scope.headerShowing = false;
                };

                $scope.goToSection = function(chapter, section) {
                    $state.go('section', {chapter: chapter, section: section});
                };

                function recalculateSizes() {
                    var maxHeight = window.innerHeight - 130;
                    $scope.isMobile = window.innerWidth <= 1025;
                    headerHeight = $$header.height();
                    staticHeight = $$headerStatic.height();

                    if ($scope.currentSection) {
                        $$headerSectionsList = $('.header__sections-list', element);
                        $timeout(function() {
                            sectionsListHeight = $$headerSectionsList.innerHeight() - 1;
                            if (sectionsListHeight > maxHeight) {
                                $('.header__sections-list__links').height(maxHeight);
                            }
                            // calculate again so that number is definitely right
                            // even if state change coincided with a collapse animation
                            // or came from navigation using the flyout
                            $timeout(function() {
                                sectionsListHeight = $$headerSectionsList.innerHeight() - 1;
                                if (sectionsListHeight > maxHeight) {
                                    $('.header__sections-list__links').height(maxHeight).css('overflow', 'auto');
                                }
                            }, 300);
                        }, 100);
                    }
                }

                function changeColors(color) {
                    $$headerMain.css('background', color);
                    $$headerSection.css('background', color);
                    $$titleNumber.css('color', color);
                }

                function findHomeSections() {
                    chapters.get().then(function(response) {
                        $scope.homeChapters = _.map(response, function(chapter) {
                            return {
                                id: chapter.slug,
                                background: COLORS[chapter.slug],
                                title: {
                                    name: chapter.name,
                                    number: '0' + chapter.number
                                },
                                sections: chapter.sections
                            };
                        });
                    });

                    sections.get().then(function(response) {
                        $scope.sections = response;
                    });
                }

                function homeSectionSizes() {
                    _.each($scope.homeChapters, function(element) {
                        var $$element = $('#' + element.id);
                        var itemPositionStart;
                        var elementHeight;
                        var itemPositionEnd;

                        if (!$$element.length) {
                            return;
                        }

                        itemPositionStart = $$element.position().top;
                        elementHeight = $$element.height();
                        itemPositionEnd = itemPositionStart + elementHeight;
                        element.position = {
                            start: itemPositionStart,
                            end: itemPositionEnd
                        };
                    });
                }

                function homeScrolled() {
                    var loc = $$window.scrollTop();
                    if ((loc > staticHeight) !== $scope.headerShowing) {
                        $scope.headerShowing = loc > staticHeight;
                        $scope.headerStaticShowing = loc <= staticHeight;
                        $scope.$applyAsync();
                    }
                }

                function sectionScrolled() {
                    var loc = $$window.scrollTop();
                    var shouldCollapse = loc > sectionHeaderCollapseMin;

                    if ($scope.currentSection && $scope.currentSection.slug === 'elements-of-a-suit') {
                        shouldCollapse = loc > 360;
                    }

                    if (shouldCollapse !== $scope.headerSectionCollapsed) {
                        $scope.headerSectionCollapsed = shouldCollapse;
                        $scope.headerSectionShowing = !shouldCollapse;
                    }
                    if ($scope.headerSectionsListShowing) {
                        $scope.toggleSectionsList();
                    }
                    $scope.$applyAsync();
                }

                function articleScrolled() {
                    if ($scope.headerSectionsListShowing) {
                        $scope.toggleSectionsList();
                        $scope.$applyAsync();
                    }
                }

                function changeHeader(section) {
                    if (!section.background) {
                        changeColors('transparent');
                        $$headerInner.removeClass('has-border');
                    } else {
                        // make the section have solid background
                        changeColors(section.background);
                        $$headerInner.addClass('has-border');
                    }
                    if (section.id) {
                        $scope.chapterSlug = section.id;
                    }
                    if (section.title) {
                        $scope.title = section.title;
                        $scope.showTitle = true;
                    } else {
                        $scope.showTitle = false;
                    }
                    $scope.headerCurrentSection = section;
                    $scope.$applyAsync();
                }

                $scope.toggleSectionsList = function() {
                    TweenMax.killTweensOf($$headerSectionsList);
                    if ($scope.headerSectionsListShowing) {
                        hideSectionsList();
                    } else {
                        showSectionsList();
                    }
                    $scope.headerSectionsListShowing = !$scope.headerSectionsListShowing;
                };

                function showSectionsList() {
                    TweenMax.to($$headerSectionsList, 0.3, {
                        bottom: -sectionsListHeight,
                        onComplete: function() {
                            $timeout(function() {
                                TweenMax.set($$headerSectionsList, {top: 64});
                                TweenMax.set('.header__sections-list__links', {
                                    bottom: 0,
                                    left: 0,
                                    right: 0,
                                    top: 0,
                                    position: 'absolute',
                                    overflow: 'auto'
                                });
                            }, 300);
                        }
                    });
                }

                function hideSectionsList() {
                    $scope.headerSectionsListClosing = true;
                    $('.header__sections-list__subnav__category').removeClass('subnav-expanded');
                    TweenMax.set($$headerSectionsList, {top: ''});
                    $('.header__sections-list__links').removeAttr('style');
                    TweenMax.to($$headerSectionsList, 0.3, {bottom: 0, onComplete: function() {
                        TweenMax.set($$headerSectionsList, {bottom: ''});
                        $timeout(function() {
                            $scope.headerSectionsListClosing = false;
                            $scope.$applyAsync();
                        }, 150);
                    }});
                }

                $scope.toggleSubnavCategory = function(e) {
                    var $el = $(e.currentTarget);
                    var $category = $el.parent();
                    var isExpanded = $category.hasClass('subnav-expanded');
                    $('.header__sections-list__subnav__category').removeClass('subnav-expanded');
                    if (!isExpanded) {
                        $category.addClass('subnav-expanded');
                    }
                };

                $scope.$on('duScrollspy:becameActive', function($event, $element, $target) {
                    var id = $target.attr('id');
                    var section = _.findWhere($scope.homeChapters, {id: id});
                    if ($state.current.name === 'home') {
                        changeHeader(section);
                    }
                });

                $scope.$on('duScrollspy:becameInactive', function($event, $element, $target) {
                    if (!$scope.currentSection) {
                        changeHeader(false);
                    }
                });

                function calculatePosition() {

                    var style;
                    var matrix;
                    var scrollY;
                    var homeSection = false;

                    if (!pageElement) {
                        return;
                    }

                    style = window.getComputedStyle(pageElement);

                    if (browser.isIe) {
                        scrollY = parseInt(style.top, 10);
                    } else {
                        matrix = new WebKitCSSMatrix(style.webkitTransform);
                        scrollY = matrix.m42;
                    }

                    if (scrollY === 0 && $$window.scrollTop() > 100) {
                        return;
                    }

                    scrollY = -scrollY + 20;
                    _.each($scope.homeChapters, function(element) {
                        if (element.position && scrollY >= element.position.start && scrollY <= element.position.end) {
                            homeSection = element;
                        }
                    });

                    if (
                        ($scope.headerCurrentSection && $scope.headerCurrentSection.id !== homeSection.id) || // changing to different section
                        ($scope.headerCurrentSection && homeSection === false) || // changing from section to blank
                        (!$scope.headerCurrentSection && homeSection) // changing from blank to section
                    ) {
                        changeHeader(homeSection);
                    }
                }

                $scope.$on('smoothscroll:scrollStart', function(e, args) {
                    var page = args.page;
                    if (page && !isScrolling) {
                        isScrolling = true;

                        if ($state.current.name === 'home' && page.get(0)) {
                            homeSectionSizes();
                            pageElement = page.get(0);
                            clearInterval(calculateInterval);
                            calculateInterval = setInterval(calculatePosition, calculateTime);
                        }
                    }
                });

                $scope.$on('smoothscroll:scrollComplete', function(e, args) {
                    var page = args.page;
                    if (page) {
                        isScrolling = false;
                        clearInterval(calculateInterval);
                        if ($state.current.name === 'home') {
                            pageElement = page.get(0);
                        }
                    }
                });

                $scope.$on('$stateChangeSuccess', function(e, toState, toStateParams, fromState, fromStateParams) {
                    $scope.lastState = $rootScope.pagePositions[0];
                    if ($scope.page !== toState.name || ($scope.currentChapter !== toStateParams.chapter)) {
                        if (toState.name === 'home') {
                            if (!toStateParams.chapter || toStateParams.chapter === '') {
                                // going to top of home page
                                $scope.headerStaticShowing = true;
                                changeHeader(false);
                            } else {
                                $scope.headerStaticShowing = false;
                                homeSectionSizes();
                                calculatePosition();
                            }
                        } else if (toStateParams.chapter) {
                            // inside a chapter or article
                            $scope.headerShowing = true;
                            changeHeader(_.find($scope.homeChapters, {id: toStateParams.chapter}));
                        } else if (toState.name === 'how-to-fold-a-pocket-square' || toState.name === 'how-to-tie-bow-tie' || toState.name === 'how-to-lace') {
                            $scope.headerShowing = true;
                            toStateParams.chapter = 'style-guide';
                            toStateParams.section = 'elements-of-a-suit';
                            changeHeader(_.find($scope.homeChapters, {id: toStateParams.chapter}));
                        }
                    }

                    $scope.currentChapter = toStateParams.chapter ? _.find($scope.homeChapters, {id: toStateParams.chapter}) : null;
                    $scope.currentSection = toStateParams.section ? _.find($scope.sections, {slug: toStateParams.section}) : null;
                    $scope.currentArticle = toStateParams.article ? _.find($scope.currentSection.modules, {slug: toStateParams.article}) : null;

                    if (toState.name === 'how-to-fold-a-pocket-square' || toState.name === 'how-to-tie-bow-tie' || toState.name === 'how-to-lace') {
                        $scope.currentArticle = true;
                    }

                    if ($scope.currentSection) {
                        if (!$scope.currentArticle) {
                            $scope.chapterNav = angular.element(document.querySelectorAll('.section')[0]).scope().sectionFooterData;
                        }
                        $scope.headerSectionShowing = true;

                        $scope.lastSectionOfChapter = $scope.currentChapter.sections[$scope.currentChapter.sections.length - 1] === $scope.currentSection;
                    } else {
                        $scope.headerSectionShowing = false;
                    }

                    if ($scope.currentArticle || $scope.isMobile) {
                        $scope.headerSectionShowing = false;
                        $scope.headerSectionCollapsed = true;
                    } else {
                        $scope.headerSectionCollapsed = false;
                    }

                    if ($scope.headerSectionsListShowing) {
                        $scope.toggleSectionsList();
                    }

                    $scope.page = toState.name;

                    recalculateSizes();
                });

                $scope.$on('app:scrolled', function() {
                    // if ($scope.isMobile) {
                    //     return;
                    // }

                    if ($scope.page === 'home') {
                        homeScrolled();
                    } else if ($scope.isMobile) {
                        return;
                    } else if ($scope.page === 'section') {
                        sectionScrolled();
                    } else if ($scope.page === 'article') {
                        articleScrolled();
                    }
                });

                $scope.$on('app:resized', function() {
                    if ($state.current.name === 'home') {
                        homeSectionSizes();
                    }
                });

                findHomeSections();
                recalculateSizes();
            }
        };
    }])
;

