/* global angular */
angular.module('ua5App')
    .directive('navbar', ['$rootScope', '$state', '$timeout', 'chapters', function($rootScope, $state, $timeout, chapters) {
        return {
            restrict: 'A',
            templateUrl: 'components/navbar/navbar.html',
            link: function($scope, element, attrs) {
                $scope.$on('$stateChangeSuccess', function(e, toState, toStateParams) {
                    $scope.current = toStateParams.chapter;

                    //This fixes a doubling up 'sticky' issue that happens
                    //intermittently while transitioning
                    setTimeout(function() {
                        $scope.current = toStateParams.chapter;
                    }, 500);
                });
                chapters.get().then(function(response) {
                    $scope.chapters = response;
                });
                $scope.$on('duScrollspy:becameActive', function($event, $element, $target) {
                    $scope.topOfPage = false;
                    if ($state.current.name === 'home') {
                        $scope.current = '';
                        //Scroll clears out the deep link
                        // you dont need to update the url
                        // of these sections on home
                        // it's too much history
                        $state.go('home', {
                            chapter: ''
                        }, {
                            notify: false
                        });
                    }
                });
                $scope.openFlyout = function() {
                    $rootScope.$broadcast('flyout:open');
                };

                $scope.gotoChapter = function(slug) {
                    if ($state.current.name !== 'home') {
                        $state.go('home', {
                            chapter: slug
                        });
                    } else {
                        $rootScope.$broadcast('nav:chapter', slug);
                    }
                };

                $scope.state = $state;
                $scope.scrollSpyEnabled = true;
                $timeout(function() {
                    if ($state.current.name === 'home') {
                        $scope.topOfPage = true;
                    }
                }, 100);
                $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams) {
                    if (toState.name === 'home') {
                        $scope.scrollSpyEnabled = true;
                        $scope.topOfPage = true;
                    } else {
                        $scope.scrollSpyEnabled = false;
                        $scope.topOfPage = false;
                    }
                });

            }
        };
    }])
;

