/* global angular, $, WebKitCSSMatrix */
angular.module('ua5App')
    .directive('parallaxer', ['$window', function($window) {
        return {
            restrict: 'A',
            link: function($scope, element, attrs) {
                var $$window = $($window);
                var $$element = $(element);
                var $$background = $('.parallaxer__background', element);

                var pageElement;
                var calculateInterval = null;
                var calculateTime = 20;
                var parallaxPercentMargin = 40;

                var itemPositionStart;
                var elementHeight;
                var windowHeight;

                var isScrolling = false;

                var browser = {
                    isIe: (navigator.userAgent.indexOf('MSIE ') > 0) || !!navigator.userAgent.match(/Trident.*rv\:11\./)
                };

                function calculatePosition() {

                    var style = window.getComputedStyle(pageElement);
                    var matrix;
                    var scrollY;
                    var progress;
                    var percent;
                    var itemPositionEnd = itemPositionStart + elementHeight;
                    var viewportStart;
                    var viewportEnd;

                    if (browser.isIe) {
                        scrollY = parseInt(style.top, 10);
                    } else {
                        matrix = new WebKitCSSMatrix(style.webkitTransform);
                        scrollY = matrix.m42;
                    }

                    viewportStart =  - scrollY;
                    viewportEnd =  - scrollY + windowHeight;

                    if (
                        (viewportStart < itemPositionStart && viewportEnd > itemPositionStart) ||
                        (viewportStart < itemPositionEnd && viewportEnd > itemPositionEnd) ||
                        (viewportStart > itemPositionStart && viewportEnd < itemPositionEnd)
                    ) {
                        progress = (itemPositionEnd - viewportStart) / (windowHeight + elementHeight);
                        percent = -((1 - progress) * parallaxPercentMargin);

                        $$background.css('top', percent + '%');
                    }
                }

                $scope.$on('smoothscroll:scrollStart', function(e, args) {
                    var page = args.page;
                    if (page && !isScrolling) {
                        isScrolling = true;
                        pageElement = page.get(0);

                        elementHeight = $$element.height();
                        itemPositionStart = $$element.position().top;
                        windowHeight = $$window.height();

                        clearInterval(calculateInterval);
                        calculateInterval = setInterval(calculatePosition, calculateTime);
                    }
                });

                $scope.$on('smoothscroll:scrollComplete', function(e, args) {
                    var page = args.page;
                    if (page) {
                        isScrolling = false;
                        clearInterval(calculateInterval);
                        pageElement = page.get(0);

                        elementHeight = $$element.height();
                        itemPositionStart = $$element.position().top;
                    }
                });
            }
        };
    }])
;

