/* global angular, ScrollReveal */
angular.module('ua5App')
    .directive('scrollReveal', [function() {
        return {
            restrict: 'A',
            scope: {
                scrollRevealDelay: '@',
                scrollRevealDistance: '@',
                scrollRevealDuration: '@',
                scrollRevealEase: '@',
                scrollRevealOrigin: '@',
                scrollRevealStartingOpacity: '@',
                scrollRevealReset: '@'
            },
            link: function($scope, element, attrs) {
                if (!window.scrollReveal) {
                    window.scrollReveal = new ScrollReveal();
                }

                $scope.scrollRevealDelay = $scope.scrollRevealDelay || 300;
                $scope.scrollRevealOrigin = $scope.scrollRevealOrigin || 'bottom';
                $scope.scrollRevealDistance = $scope.scrollRevealDistance || 0;
                $scope.scrollRevealDuration = $scope.scrollRevealDuration || 700;
                $scope.scrollRevealEase = $scope.scrollRevealEase || 'cubic-bezier(0.200, 0.485, 0.205, 1)';
                $scope.scrollRevealStartingOpacity = $scope.scrollRevealStartingOpacity || 0;
                $scope.scrollRevealReset = $scope.scrollRevealReset || false;

                window.scrollReveal.reveal(element, {
                    origin: $scope.scrollRevealOrigin,
                    distance: $scope.scrollRevealDistance,
                    duration: $scope.scrollRevealDuration,
                    delay: $scope.scrollRevealDelay,
                    easing: $scope.scrollRevealEase,
                    opacity: $scope.scrollRevealStartingOpacity,
                    reset: $scope.scrollRevealReset,
                    scale: 1,
                    viewOffset: {top: 100}
                });
            }
        };
    }])
;

