/* global angular, $ */
angular.module('ua5App')
    .directive('slideshow', ['$timeout', 'BREAKPOINTS', function($timeout, BREAKPOINTS) {
        return {
            restrict: 'A',
            templateUrl: 'components/slideshow/slideshow.html',
            scope: {
                slideshowData: '='
            },
            link: function($scope, element, attrs) {
                var $$slideshow = $('.slideshow__slides', element);
                var sLength = $scope.slideshowData.slides.length;
                $scope.slideshow = $scope.slideshowData;
                $scope.slides = $scope.slideshowData.slides;
                $scope.isDark = {
                    prev: $scope.slides[sLength - 1].color === 'dark',
                    current: $scope.slides[0].color === 'dark',
                    next: $scope.slides[1].color === 'dark'
                };

                $timeout(function() {

                    var responsiveArr = [];

                    if ($scope.slideshow.mobileExpand) {
                        responsiveArr.push({
                            breakpoint: BREAKPOINTS.PHABLET + 1,
                            settings: 'unslick'
                        });
                    }

                    $$slideshow.slick({
                        slidesToShow: 1,
                        arrows: true,
                        centerMode: true,
                        centerPadding: $scope.slideshow.sidePadding ? $scope.slideshow.sidePadding : '10%',
                        prevArrow:  $('.slideshow__button--prev', element),
                        nextArrow:  $('.slideshow__button--next', element),
                        responsive: responsiveArr
                    });

                    $$slideshow.on('beforeChange', function(event, slick, currentSlide, nextSlide) {
                        $scope.isDark = {
                            prev: $scope.slides[((nextSlide - 1 >= 0) ? nextSlide - 1 : sLength - 1)].color === 'dark',
                            current: $scope.slides[nextSlide].color === 'dark',
                            next: $scope.slides[((nextSlide + 1 < sLength) ? nextSlide + 1 : 0)].color === 'dark'
                        };

                        $scope.$applyAsync();
                    });
                });
            }
        };
    }])
;

/* global angular, $, TweenMax, Quint, Modernizr */
angular.module('ua5App')
    .directive('smoothPageScroll', ['$window', '$rootScope', function($window, $rootScope) {
        return {
            restrict: 'A',
            transclude: true,
            templateUrl: 'components/smooth-page-scroll/smooth-page-scroll.html',
            link: function($scope, element, attrs) {
                var $$page = $(element).find('.smooth-page-scroll__page');
                var $$sizer = $(element).find('.smooth-page-scroll__size');
                var $window = $(window);
                var enabled = true;

                var browser = {
                    isIe: (navigator.userAgent.indexOf('MSIE ') > -1) || !!navigator.userAgent.match(/Trident.*rv\:11\./),
                    isFF: (navigator.userAgent.indexOf('Firefox') > -1)
                };

                function syncSize() {
                    $$sizer.height($$page.height());
                }

                //firefox gets wonky, and we dont want it on mobile
                if (!Modernizr.touch && !browser.isFF) {
                    $$page.css('position', 'fixed');

                    $scope.$on('app:resized', function() {
                        syncSize();
                    });

                    $scope.$watch(function() {
                            return $$page.height();
                        },
                        function(newValue, oldValue) {
                            if (newValue !== oldValue) {
                                syncSize();
                            }
                        }
                    );

                    $scope.$on('app:scrolled', function() {
                        if (enabled) {
                            var tweenObj = {
                                ease: Quint.easeOut,
                                overwrite: 'all',
                                onComplete: function() {
                                    $rootScope.$broadcast('smoothscroll:scrollComplete', {page: $$page});
                                }
                            };

                            $rootScope.$broadcast('smoothscroll:scrollStart', {page: $$page});

                            if (browser.isIe) {
                                tweenObj.top = -$($window).scrollTop();
                            } else {
                                tweenObj.y = -$($window).scrollTop();
                            }

                            TweenMax.to($$page, 1, tweenObj);
                        }
                    });

                    $scope.$on('home:scrollPositionSet', function() {
                        if (browser.isIe) {
                            TweenMax.set($$page, {
                                top: -$($window).scrollTop()
                            });
                        } else {
                            TweenMax.set($$page, {
                                y: -$($window).scrollTop()
                            });
                        }
                    });

                    $scope.$on('app:transition:finish', function() {
                        enabled = true;
                        $scope.$broadcast('app:scrolled');
                    });

                    $scope.$on('app:transition:start', function() {
                        enabled = false;
                    });
                }
            }
        };
    }])
;

