/* global angular */
angular.module('ua5App')
    .directive('tout', ['$stateParams', '$state', function($stateParams, $state) {
        return {
            restrict: 'A',
            templateUrl: 'components/tout/tout.html',
            scope: {
                toutData: '='
            },
            link: function($scope) {
                $scope.chapter = $stateParams.chapter;
                $scope.section = $stateParams.section;
                $scope.modifierClasses = [];
                if ($scope.toutData.backgroundColor) {
                    $scope.modifierClasses.push('tout--color');
                }
                if ($scope.toutData.noMaxWidth) {
                    $scope.modifierClasses.push('tout--no-max');
                }
                if ($scope.toutData.toutStyle) {
                    $scope.modifierClasses.push('tout--' + $scope.toutData.toutStyle);
                }
                if ($scope.toutData.chapter) {
                    $scope.modifierClasses.push('tout--' + $scope.toutData.chapter);
                }
                // If there is a relatedImage, use it over anything, if there
                // isn't then fallback to the regular image.
                if ($scope.toutData.relatedImage && $scope.toutData.relatedImage.url) {
                    $scope.modifierClasses.push('tout--related-image');
                    $scope.toutData.image.url = $scope.toutData.relatedImage.url;
                    $scope.toutData.image.width = $scope.toutData.relatedImage.width;
                }

                $scope.goToLink = function() {
                    if ($scope.toutData.slug) {
                        $state.go('article', {chapter: $scope.toutData.chapter, section: $scope.toutData.section, article: $scope.toutData.slug});
                    } else if ($scope.toutData.state) {
                        $state.go($scope.toutData.state);
                    }
                };
            }
        };
    }])
;

