/* global angular, _ */
angular.module('ua5App.article')
    .config(['$stateProvider', '$urlRouterProvider', function($stateProvider, $urlRouterProvider) {
        $stateProvider.state('article', {
            url: '/:chapter/:section/:article',
            templateUrl: 'states/article/article.html',
            controller: 'articleCtrl',
            controllerAs: 'ctrl',
            resolve: {
                delay: ['$q', '$timeout', function($q, $timeout) {
                    var deferred = $q.defer();
                    $timeout(function() {
                        deferred.resolve();
                    });
                    return deferred.promise;
                }],
                chapters: ['chapters', function(chapters) {
                    return chapters.get().then(function(response) {
                        return response;
                    });
                }],
                sections: ['sections', function(sections) {
                    return sections.get().then(function(response) {
                        return response;
                    });
                }],
                imageMaps: ['imageMaps', function(imageMaps) {
                    return imageMaps.get().then(function(response) {
                        return response;
                    });
                }]
            }
        });
    }])
    .controller('articleCtrl', ['$scope', '$stateParams', 'chapters', 'sections', 'imageMaps', 'ngMeta', function($scope, $stateParams, chapters, sections, imageMaps, ngMeta) {
        var articleParam;
        var chapterParam;
        var sectionParam;
        chapterParam = $stateParams.chapter;
        sectionParam = $stateParams.section;
        articleParam = $stateParams.article;

        $scope.chapterParam = chapterParam;
        $scope.imageMap = _.findWhere(imageMaps, {id: articleParam});
        $scope.chapter = _.findWhere(chapters, {
            slug: chapterParam
        });
        $scope.section = _.findWhere(sections, {
            slug: sectionParam
        });
        $scope.article = _.find($scope.section.modules, function(module) {
            return module.slug === articleParam;
        });
        $scope.relatedArticles = {
            header: $scope.article.relatedTitle,
            articles: $scope.article.related
        };
        ngMeta.setTitle($scope.article.metaTitle);
        ngMeta.setTag('description', $scope.article.metaDescription);
        ngMeta.setTag('image', $scope.article.metaImage);
        ngMeta.setTag('url', 'https://theblacktux.com/groomsguide/' + chapterParam + '/' + sectionParam + '/' + articleParam);
    }])
;

