angular.module('ua5App.section')
    .config(['$stateProvider', '$urlRouterProvider', function($stateProvider, $urlRouterProvider) {
        $stateProvider.state('section', {
            url: '/:chapter/:section',
            templateUrl: 'states/section/section.html',
            controller: 'sectionCtrl',
            controllerAs: 'ctrl',
            resolve: {
                delay: ['$q', '$timeout', function($q, $timeout) {
                    var deferred = $q.defer();
                    $timeout(function() {
                        deferred.resolve();
                    });
                    return deferred.promise;
                }],
                chapters: ['chapters', function(chapters) {
                    return chapters.get().then(function(response) {
                        return response;
                    });
                }],
                sections: ['sections', function(sections) {
                    return sections.get().then(function(response) {
                        return response;
                    });
                }]
            }
        });
    }])
    .controller('sectionCtrl', ['$scope', '$stateParams', 'chapters', 'sections', 'ngMeta', '$rootScope', '$timeout', function($scope, $stateParams, chapters, sections, ngMeta, $rootScope, $timeout) {
        var chapterParam;
        var sectionParam;
        var sectionIndex;
        var lastChapterSections;
        chapterParam = $stateParams.chapter;
        sectionParam = $stateParams.section;

        $scope.$on('app:transition:finish', function() {
            if (
                $rootScope.pagePositions[1] && _.isEqual($rootScope.pagePositions[1].params, $stateParams) &&
                $rootScope.pagePositions[0] && $rootScope.pagePositions[0].name === 'article'
            ) {
                // coming back from article
                $(window).scrollTop($rootScope.pagePositions[1].location);
            }
        });

        $scope.chapter = _.findWhere(chapters, {
            slug: chapterParam
        });

        $scope.section = _.find($scope.chapter.sections, function(section, i) {
            sectionIndex = i;
            return section.slug === sectionParam;
        });

        if (!$scope.section) {
            $scope.section = _.find(sections, function(section) {
                return section.slug === sectionParam;
            });
        }

        function createSectionFooterData() {
            var prev, next, current;

            if (typeof(sectionIndex) === 'undefined') {
                return false;
            }

            if (sectionIndex === 0) {
                if ($scope.chapter.number === 1) {
                    prev = null;
                } else {
                    lastChapterSections = chapters[$scope.chapter.number - 2].sections;
                    prev = lastChapterSections[lastChapterSections.length - 1];
                    prev.chapter = chapters[$scope.chapter.number - 2];
                }
            } else {
                prev = $scope.chapter.sections[sectionIndex - 1];
                prev.chapter = $scope.chapter;
            }

            if (sectionIndex === $scope.chapter.sections.length - 1) {
                if ($scope.chapter.number === chapters.length) {
                    next = null;
                } else {
                    next = chapters[$scope.chapter.number].sections[0];
                    if (next) {
                        next.chapter = chapters[$scope.chapter.number];
                    }
                }
            } else {
                next = $scope.chapter.sections[sectionIndex + 1];
                next.chapter = $scope.chapter;
            }

            current = $scope.section;
            current.chapter = $scope.chapter;

            $scope.sectionFooterData = {
                prev: prev,
                next: next,
                current: current
            };

            if (!next && current.chapter.slug === 'groomsmen-guide') {
                $scope.sectionFooterData.special = true;
            }
        }

        createSectionFooterData();
        if ($scope.section) {
            ngMeta.setTitle($scope.section.metaTitle);
            ngMeta.setTag('description', $scope.section.metaDescription);
            ngMeta.setTag('image', $scope.section.metaImage);
            ngMeta.setTag('url', 'https://theblacktux.com/groomsguide/' + chapterParam + '/' + sectionParam);
        }

        $timeout(function() {
            $('.section').imagesLoaded(function() {
                if (window.location.hash && $('[name="' + window.location.hash.substring(1) + '"]').length) {
                    $(window).scrollTop($('[name="' + window.location.hash.substring(1) + '"]').offset().top - 70);
                }
            });
        }, 1000);
    }])
;

