  $templateCache.put('components/tux-vs-suit/tux-vs-suit.html',
    "<div class=\"tux-vs-suit\">\n" +
    "    <div class=\"tux-vs-suit__outer\">\n" +
    "        <div class=\"tux-vs-suit__inner\">\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div class=\"tux-vs-suit__title\">\n" +
    "                    What Should I Wear?\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div class=\"tux-vs-suit__description\">\n" +
    "                    Tuxedos are more formal than suits. You probably already knew this, and if you didn’t—where have you been? But while you’re well aware that the tuxedo is the suit’s older and more sophisticated cousin, you may not know exactly what distinguishes the two from each other. Here’s the breakdown:\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div\n" +
    "                class=\"tux-vs-suit__hero\"\n" +
    "                style=\"background-image:url(../assets/img/tux-vs-suit/hero-1.jpg)\">\n" +
    "            </div>\n" +
    "\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div class=\"tux-vs-suit__text\">\n" +
    "                    <p>Unlike suits, tuxedos have satin lapels, as well as satin stripes down the sides of the pants.</p>\n" +
    "                    <p>Tuxedos are traditionally worn with <a href=\"https://theblacktux.com/collection/neckwear-2\" target=\"_blank\">Bow Ties</a>, while suits are worn with <a href=\"https://theblacktux.com/collection/neckwear-2\" target=\"_blank\">Neckties</a>—but in this day and age, the rules are loosening up, and your neck-embellishment options have become a little more interchangeable.</p>\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div\n" +
    "                    class=\"tux-vs-suit__row__half\"\n" +
    "                    product-image\n" +
    "                    product-image-src=\"../assets/img/tux-vs-suit/tux-jacket.jpg\"\n" +
    "                    product-image-title=\"Notched Lapel Black Tuxedo Jacket\"\n" +
    "                    product-image-link=\"https://theblacktux.com/collection/tuxedos/notched-lapel-tuxedo\">\n" +
    "                </div>\n" +
    "                <div\n" +
    "                    class=\"tux-vs-suit__row__half\"\n" +
    "                    product-image\n" +
    "                    product-image-src=\"../assets/img/tux-vs-suit/tux-pants.jpg\"\n" +
    "                    product-image-title=\"Black Tuxedo Pants\"\n" +
    "                    product-image-link=\"https://theblacktux.com/collection/tuxedos/notched-lapel-tuxedo\">\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div class=\"tux-vs-suit__text\">\n" +
    "                    <p>A tuxedo worn with a bow tie calls for a formal tuxedo shirt with a <a href=\"https://theblacktux.com/collection/shirts/classic-tuxedo-shirt\" target=\"_blank\">Wing Tip Collar</a>. You’d never wear a wing tip collar with a suit—even if you chose to wear a bow tie, you’d wear it with a regular spread collar.</p>\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div\n" +
    "                    class=\"tux-vs-suit__row__full\"\n" +
    "                    product-image\n" +
    "                    product-image-src=\"../assets/img/tux-vs-suit/shirt.jpg\"\n" +
    "                    product-image-title=\"Cotton Wing Tip Shirt\"\n" +
    "                    product-image-link=\"https://theblacktux.com/collection/shirts/classic-tuxedo-shirt\">\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div class=\"tux-vs-suit__text\">\n" +
    "                    <p>Your <a href=\"https://theblacktux.com/collection/shoes\" target=\"_blank\">Shoe</a> options for suits are pretty much endless—oxfords, slippers, loafers—while tuxedos are traditionally worn with black patent oxfords. Again, rules are (sometimes) meant to be broken, and today’s men are branching out more with their footwear, even if they opt for a tuxedo. Do <em>You</em>.</p>\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div\n" +
    "                    class=\"tux-vs-suit__row__half\"\n" +
    "                    product-image\n" +
    "                    product-image-src=\"../assets/img/tux-vs-suit/tux-shoes-1.jpg\"\n" +
    "                    product-image-title=\"Cap Toe Shoes in Patent/Calf\"\n" +
    "                    product-image-link=\"https://theblacktux.com/collection/shoes/cap-toe-shoes\">\n" +
    "                </div>\n" +
    "                <div\n" +
    "                    class=\"tux-vs-suit__row__half\"\n" +
    "                    product-image\n" +
    "                    product-image-src=\"../assets/img/tux-vs-suit/tux-shoes-2.jpg\"\n" +
    "                    product-image-title=\"Black Patent Leather Shoes\"\n" +
    "                    product-image-link=\"https://theblacktux.com/collection/shoes/black-patent-leather-shoes\">\n" +
    "                </div>\n" +
    "            </div>\n" +
    "\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div class=\"tux-vs-suit__text\">\n" +
    "                    <p>So should you wear a tuxedo or a suit to your event? Only you can answer that question. Look deep inside yourself, meditate, wander off into the desert on a spiritual quest for which formalwear best calls to your soul. Or, you know, just take a look at the event, its <a ui-sref=\"section({ chapter: 'style-guide', section: 'dress-codes'})\">Dress Code</a> (if there is one), and what the other attendees are wearing—and follow accordingly.</p>\n" +
    "                </div>\n" +
    "            </div>\n" +
    "            <div\n" +
    "                class=\"tux-vs-suit__hero\"\n" +
    "                style=\"background-image:url(../assets/img/tux-vs-suit/hero-2.jpg)\">\n" +
    "            </div>\n" +
    "            <div class=\"tux-vs-suit__row\">\n" +
    "                <div class=\"tux-vs-suit__text\">\n" +
    "                    <p>If you’re a groom and your bride is wearing a princess gown in your wedding, you may look too casual standing next to her in a suit. Go for a tux. Conversely, if you’re having a sunset beach wedding and the bride’s in a flowy, less- traditional dress, you’ll probably look like a buttoned-up fool in a black tuxedo. Go for a suit. These are just things to consider. In the end, it’s your wedding and you can wear whatever you want—as long as it’s not a tuxedo with flip-flops.</p>\n" +
    "                    <p class=\"tux-vs-suit__bottom-line\">PLEASE DON’T WEAR A TUXEDO WITH FLIP-FLOPS.</p>\n" +
    "                </div>\n" +
    "            </div>\n" +
    "        </div>\n" +
    "    </div>\n" +
    "</div>\n"
  );


